.services {
  margin-top: 28px;
}

.services__header {
  width: 320px;
  margin: 0 auto;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
  margin-bottom: 28px;
}

.services__list {
  padding: 0;
  width: 320px;
  margin: 0 auto;
  color: white;
  list-style:none;
  padding-top: 23px;
  margin-left: 15px;
}

.services__wrapper {
  background-color: #0055b2;
}

.services__background {
  background-image: url("../img/Векторный смарт-объект.png");
  background-repeat: no-repeat;
  margin: 0 auto;
  width: 320px;
  padding-bottom: 12px;
}

.services__item {
  width: 182px;
  font-size: 14px;
  font-weight: lighter;
  line-height: 18px;
  letter-spacing: 0.84px;
  margin-bottom: 17px;
}

.services__arrow-white {
  margin-top: 4px;
  margin-left: 15px;
  margin-bottom: 25px;
}

.services__list-two {
  padding: 0;
  width: 320px;
  margin: 0 auto;
  color: black;
  list-style:none;
  display: flex;
  flex-wrap: wrap;
}

.services__item-two {
  width: 110px;
  height: 80px;
  padding-right: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 14px;
  margin-top: 20px;
}

.services__list-two--desk,
.services__header--desk {
  display: none;
}

.services__address {
  display: none;
}

@media (min-width: $desktop-width) {
  .services {
    margin-top: -172px;
  }

  .services__address {
    display: block;
    width: 214px;
    height: 343px;
    background-color: rgb(56, 56, 56);
    background-image: url("../img/contact.png");
    background-size: cover;
    background-repeat: no-repeat;
    margin-top: 170px;
    margin-left: -3px;
  }

  .menu-description--services {
    margin-top: 64px;
    margin-left: 10px;
  }

  .menu-description__item--services {
    color: white;
  }

  .menu-description__item--services a {
    color: white;
    font-size: 15px;
  }

  .hidden-menu__button--services {
    margin-left: 26px;
    background-color: transparent;
    color: white;
  }

  .services__list-two--desk {
    display: flex;
    width: 698px;
    padding: 0;
    margin: 0 auto;
    color: black;
    list-style:none;
  }

  .services__item {
    width: 205px;
    font-size: 16px;
    font-weight: lighter;
    line-height: 20px;
    letter-spacing: 0;
    margin-bottom: 21px;

    &--font {
      letter-spacing: 0.96px;
      margin-bottom: 34px;
    }
  }

  .services__list {
    margin-left: 23px;
    padding-top: 45px;
  }

  .services__list-two {
    margin: 0;
    width: 470px;

    &--second {
      width: 937px;
    }
  }

  .services__item-two {
    width: 157px;
    padding: 48px 39px 37px 38px;
    margin-top: 0;
    background-color: white;
    height: 90px;
  }

  .services__item-two--top {
    padding-top: 30px;
    &--second {
      padding-top: 48px;
    }
  }

  .services__item-two--bottom p {
    margin-bottom: 35px;
  }

  .services__item-two p {
    font-weight: lighter;
    font-size: 16px;
    letter-spacing: 0.96px;
    line-height: 20px;
    margin: 0;
  }

  .services__header--desk {
    display: block;
    font-size: 31px;
    font-weight: bold;
    line-height: 38px;
    letter-spacing: 2px;
    color: white;
    background-color: rgb(56, 56, 56);
  }

  .services__item-two--mob {
    display: none;
  }

  .services__wrapper {
    margin: 0;
    height: 512px;
    background-color: white;
    background-image: url("../img/bgServices.png");
    background-repeat: no-repeat;

    &--second {
      height: 342px;
      background-image: none;
    }
  }

  .services__background {
    background-image: url("../img/Векторный смарт-объект-desktop.png");
    width: 234px;
    height: 340px;
    margin-left: 236px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    &--second {
      margin-left: 0;
    }
  }

  .services__arrow-white {
    margin-left: 23px;
  }

  .services__container {
    display: flex;
    width: 1170px;
    margin: 0 auto;
  }
}
