.description__wrapper {
  width: 320px;
  margin: 0 auto;
}

.description__container {
  width: 290px;
  text-align: left;
  margin-left: 17px;
  margin-top: 55px;
  margin-bottom: 50px;
}

.description__header {
  margin: 0;
  font-weight: bold;
  font-size: 22px;
  line-height: 32px;
  margin-bottom: 20px;
}

.description__block {
  margin: 0;
  font-weight: lighter;
  font-size: 14px;
  line-height: 20px;
}

@media (min-width: $desktop-width) {
  .description__wrapper {
    width: 1170px;
  }

  .description__container {
    width: 1170px;
    text-align: left;
    margin-left: 0;
  }

  .description__header {
    font-size: 31px;
    line-height: 40px;
    margin-bottom: 25px;
  }

  .description__block {
    font-size: 16px;
    line-height: 24px;
  }
}
