.start {
  background-color: #404154;
  height: 560px;
  margin-top: -86px;
  padding-top: 86px;
  color: white;
}

.start__block {
  width: 320px;
  margin: 0 auto;
  height: 560px;
  margin-top: -86px;
  padding-top: 86px;
  background-image: url("../img/бг1.png");
  background-repeat: no-repeat;

  &--second {
    background-image: url("../img/бг1_second-page.png");
  }
}

.start__wrapper {
  width: 320px;
  margin: 0 auto;
  margin-top: 76px;
  text-align: center;
}

.start__header {
  width: 283px;
  margin-left: 20px;
  font-size: 29px;
  line-height: 35px;
  margin-bottom: 19px;
}

.start__description {
  width: 260px;
  margin-left: 31px;
  font-size: 17px;
  font-weight: lighter;
  line-height: 25px;
  margin-bottom: 40px;
}

.start__button {
  margin-bottom: 55px;
  width: 253px;
  margin-left: 34px;
  margin-right: 33px;
  font-size: 16px;
  font-weight: lighter;
  color: white;
  background-color: #f83659;
  border: none;
  padding: 21px 81px 20px 81px;
  border-radius: 50px;
}

.start__number span{
  position: relative;
  font-size: 14px;
  font-weight: lighter;
  letter-spacing: 1.12px;
}

.start__number--color::before {
  content: " ";
  position: absolute;
  top: 23px;
  left: -4px;
  width: 24px;
  height: 3px;
  background-color: #b7babf;
}

.start__number-two::before {
  content: " ";
  position: absolute;
  top: 23px;
  left: -4px;
  width: 24px;
  height: 3px;
  background-color: #b7babf;
}

.start__number-one {
  margin-right: 26px;
}

.start__number-two {
  margin-right: 23px;
  //color: #f83659;
}

.start__container svg {
  display: none;
}

@media (min-width: $desktop-width) {
  .start {
    height: 871px;
    margin-top: -86px;
    padding-top: 86px;
    color: white;
  }

  .start__block {
    width: 100%;
    background-image: url("../img/бг-desktop.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    height: 871px;
    margin-top: -86px;
    padding-top: 86px;

    &--second {
      background-image: url("../img/bg-second.png");
    }
  }

  .start__container--second {
    display: none;
  }

  .start__wrapper {
    width: 1170px;
    margin-top: 120px;
    text-align: left;
  }

  .start__header {
    width: 646px;
    font-size: 52px;
    margin-left: 0;
    margin-bottom: 31px;
    line-height: 62px;
    letter-spacing: 2px;
  }

  .start__description {
    width: 662px;
    font-size: 24px;
    margin-bottom: 50px;
    line-height: 28px;
    margin-left: 0;
  }

  .start__button {
    margin-left: 0;
    margin-bottom: 98px;
  }

  .start__container {
    display: flex;
    justify-content: space-between;
  }

  .start__container svg {
    display: block;
  }

  .start__number span:nth-child(3) {
    display: none;
  }

  .start__number--color::before {
    top: 28px;
    left: -14px;
    width: 50px;
  }

  .start__number-one {
    margin-right: 63px;
  }

  .start__number-two::before {
    top: 28px;
    left: -14px;
    width: 50px;
    background-color: #b7babf;
  }

  .start__number-two::after {
    content: " ";
    position: absolute;
    top: 28px;
    left: -14px;
    width: 24px;
    height: 3px;
    background-color: #f83659;
  }

  .start__number-two {
    color: white;
  }
}
