.schema__wrapper {
  width: 320px;
  margin: 0 auto;
  margin-bottom: 38px;
}

.schema__header {
  margin: 0;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
}

.schema__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 41px;
}

.schema__item {
  position: relative;
  width: 290px;
  height: 552px;
  margin-left: 14px;
  margin-bottom: 18px;
  border-radius: 20px;
}

.schema__item-img img {
  margin-left: 16px;
}

.schema__item1 {
  background-image: url("../img/bg3.png");
  background-repeat: no-repeat;
  background-color: #1f104f;
}

.schema__item2 {
  background-image: url("../img/бг4.png");
  background-repeat: no-repeat;
  background-color: #7c7c7c;
}

.schema-header {
  margin-top: 20px;
  margin-bottom: 30px;
  margin-left: 15px;
  color: white;
  font-size: 14px;
  line-height: 20px;
  text-transform: uppercase;
  width: 235px;
}

.schema-header--line::before {
  content: " ";
  position: absolute;
  top: 355px;
  width: 70px;
  height: 4px;
  background-color: #dddddd;
}

.schema-header--before::before {
  content: " ";
  position: absolute;
  top: 333px;
  width: 70px;
  height: 4px;
  background-color: #dddddd;
}

.schema-description {
  margin-left: 15px;
  color: white;
  font-size: 14px;
  line-height: 18px;
  font-weight: lighter;
  width: 258px;
  margin-bottom: 25px;
}

.schema-header2 {
  width: 208px;
}

.schema-description2,
.schema-header2 {
  color: black;
}

@media (min-width: $desktop-width) {
  .schema__wrapper {
    width: 1170px;
    margin-bottom: 101px;
  }

  .schema__header {
    font-size: 31px;
  }

  .schema__list {
    margin-top: 61px;
  }

  .schema__item-img img {
    margin-top: -62px;
    position: relative;
    z-index: 1;
  }

  .schema__item-img2 {
    order: 1;
    margin-left: 25px;
  }

  .schema__item {
    width: 1170px;
    height: 432px;
    margin-left: 0;
    margin-bottom: 33px;
    background-image: url("../img/bg1-desktop.png");
    background-repeat: no-repeat;
    display: flex;
    justify-content: space-between;
    border-radius: 52px;

    &--2 {
      background-image: url("../img/bg2-desktop.png");
    }
    &--3 {
      background-image: url("../img/bg3-desktop.png");
    }
    &--4 {
      background-image: url("../img/bg4-desktop.png");
    }
    &--5 {
      background-image: url("../img/bg5-desktop.png");
    }
  }

  .schema-container {
    margin-right: 58px;
  }

  .schema-container2 {
    margin-right: 0;
    margin-left: 48px;
  }

  .schema-header {
    margin-top: 85px;
    margin-bottom: 46px;
    margin-left: 0;
    font-weight: bold;
    font-size: 17px;
    line-height: 26px;
    width: 284px;
  }

  .schema-header--line::before {
    top: 158px;
  }

  .schema-header--before::before {
    top: 134px;
  }

  .schema-description {
    margin-left: 0;
    font-size: 17px;
    line-height: 23px;
    width: 411px;
    margin-bottom: 0;
  }
}
