.page-header {
  position: relative;
  margin: 0 auto;
  width: 100%;
  color: black;
}

.page-header::before {
  content: " ";
  position: absolute;
  top: 67px;
  width: 100%;
  height: 2px;
  background-color: rgb(255, 255, 255);
}

.page-header__wrapper {
  position: relative;
}

.page-header__container {
  display: flex;
  justify-content: space-between;
  padding-bottom: 13px;
  z-index: 1;
}

.page-header__phone {
  margin-top: 12px;
  z-index: 1;
}

.page-header__nav {
  z-index: 1;
}

.page-header__logo {
  margin-top: 21px;
  margin-left: 16px;
  cursor: pointer;
  z-index: 1;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }
}

.page-header__phone-container {
  display: none;
}

@media (min-width: $desktop-width) {
  .page-header__logo img {
    width: 135px;
    height: 29px;
  }

  .page-header__logo {
    margin-top: 33px;
    margin-left: 0;

  }

  .page-header__wrapper {
    width: 1170px;
    margin: 0 auto;
  }

  .page-header::before {
    top: 96px;
  }

  .page-header__phone {
    display: none;
  }

  .page-header__phone-container {
    display: flex;
    justify-content: space-between;
    width: 450px;
    margin-left: 360px;
    position: relative;
    z-index: 1;
  }

  .page-header__block {
    display: flex;
    justify-content: space-between;
    width: 220px;
    margin-top: 40px;
  }

  .page-header__block a {
    margin-top: 3px;
    font-size: 20px;
    font-weight: bold;
    color: white;
  }

  .page-header__button {
    font-size: 14px;
    letter-spacing: 1.12px;
    font-weight: lighter;
    padding: 10px 35px 10px 34px;
    border: 2px solid #f83659;
    background-color: transparent;
    color: white;
    border-radius: 50px;
    margin-top: 31px;
  }
}
