.contacts__wrapper {
  width: 320px;
  margin: 0 auto;
  text-align: center;
}

.contacts__header {
  margin-top: 35px;
  margin-bottom: 47px;
  font-weight: bold;
  font-size: 26px;
}

.contacts__list {
  list-style: none;
  margin-bottom: 19px;
  padding: 0;
}

.contacts__item-header {
  margin: 0;
  margin-bottom: 16px;
  font-weight: lighter;
  font-size: 15px;
  color: rgb(134, 134, 134);
}

.contacts__description {
  margin-left: 15px;
  width: 290px;
  display: flex;
  justify-content: space-between;

  &--1 {
    width: 300px;
  }

  &--3 {
    width: 195px;
    margin-left: 64px;
  }
}

.contacts__description p {
  margin: 0;
  margin-bottom: 13px;
  font-weight: lighter;
  font-size: 14px;
  color: black;
}

.contacts__description--2 a {
  font-weight: bold;
  text-decoration: none;
  color: black;
  margin-bottom: 13px;
  font-size: 14px;
}

.contacts__button--1 {
  font-size: 14px;
  font-weight: lighter;
  padding: 13px 62px 12px 61px;
  border: 2px solid #f83659;
  border-radius: 50px;
  margin-left: 13px;
  margin-bottom: 39px;
}

.contacts__button--2 {
  font-size: 14px;
  font-weight: lighter;
  padding: 8px 30px 10px 30px;
  border: 2px solid #f83659;
  border-radius: 50px;
  margin-left: 13px;
  margin-bottom: 42px;
}

.contacts__social {
  padding: 0;
  margin-left: 31px;
  width: 260px;
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.contacts__social-item img:hover {
  background-color: #7c7c7c;
}

@media (min-width: $desktop-width) {
  .contacts__wrapper {
    width: 1170px;
    text-align: left;
    position: relative;
  }

  .contacts__header {
    margin-top: 67px;
    margin-bottom: 103px;
    font-size: 31px;
  }

  .contacts__block {
    display: flex;
  }

  .contacts__button--1 {
    padding: 17px 52px 16px 52px;
  }

  .contacts__button--2 {
    padding: 16px 28px 16px 27px;
    margin-left: 25px;
  }

  .contacts__description {
    margin-left: 0;
  }

  .contacts__description--1 p {
    margin-left: 18px;
  }

  .contacts__social {
    position: absolute;
    top: 372px;
    left: 310px;
    width: 190px;
  }

  .contacts__social-item img {
    width: 22px;
    height: 22px;

    &--2 {
      width: 29px;
      height: 19px;
    }

    &--3 {
      width: 25px;
      height: 22px;
    }

    &--3 {
      width: 21px;
      height: 22px;
    }
  }
}
