.site-list__item a {
  color: black;
  text-decoration: none;
  font-size: 18px;
  font-weight: lighter;
  line-height: 59px;
}

.site-list__item {
  padding-right: 45px;
  text-align: left;
  padding-left: 15px;
  border-bottom: 1px solid #e9e9e9;
}

@media (min-width: $desktop-width) {
  .site-list__item {
    border-bottom: 0;
  }
}
