.slider {
  height: 520px;
  overflow: hidden;
  width: 318px;
  margin: 0 auto;
}
.slider ul {
  margin: 0;
  padding: 0;
}

.slider .bx-wrapper {
  border: none;
  box-shadow: none;
  background: none;

}

.slider .bx-wrapper img {
  width: 360px;
}

.slider .bx-wrapper .bx-pager,
.slider .bx-wrapper .bx-controls-auto {
  bottom: -69px;
}

.slider .bx-wrapper .bx-pager.bx-default-pager a:hover,
.slider .bx-wrapper .bx-pager.bx-default-pager a.active,
.slider .bx-wrapper .bx-pager.bx-default-pager a:focus {
  background: #f83659;
}

/* Пагинация */
.slider .bx-pager {
  text-align: center;
  padding: 20px 0;
}
.slider .bx-pager-item {
  display: inline-block;
  margin: 0 4px;
}
.slider .bx-pager-item a {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: #fff;
  border-radius: 50%;
  border: 1px solid #999;
  overflow: hidden;
  text-indent: 999px;
}
.slider .bx-pager-item a.active {
  background: #ffe318;
}
.slider .bx-pager-item a:hover {
  background: #f3e792;
}

.slider__item {
  width: 1170px;
}

.start__block .slider .bx-wrapper .bx-pager,
.start__block .slider .bx-wrapper .bx-controls-auto {
  bottom: -20px;
}

@media (min-width: $desktop-width) {
 .slider {
   width: 1170px;
   height: 620px;
 }

  .slider .bx-wrapper .bx-pager,
  .slider .bx-wrapper .bx-controls-auto {
    bottom: -8px;
    left: 544px;
  }

  .slider .bx-pager-item:nth-child(3) {
    display: none;
  }

}

