.page-footer {
  width: 100%;
  background-color: #0b0b0b;
}

.page-footer__wrapper {
  width: 320px;
  margin: 0 auto;
  padding-top: 43px;
  padding-bottom: 45px;
  text-align: center;
}

.page-footer__container {
  color: white;
}

.page-footer__container p {
  font-size: 13px;
  font-weight: lighter;
  color: rgb(124, 124, 124);
}

@media (min-width: $desktop-width) {
  .page-footer {
    position: relative;
    z-index: 10000;
  }

  .page-footer__wrapper {
    width: 1170px;
    padding-top: 63px;
    padding-bottom: 70px;
    display: flex;
    justify-content: space-between;
  }

  .page-footer__container {
    display: flex;
    justify-content: space-between;
    width: 684px;
  }

  .page-footer__container p {
    font-size: 14px;
  }
}
