.form__application {
  width: 100%;
  margin: 0 auto;
  background-image: url("../img/bg-form.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.application__wrapper {
  width: 320px;
  margin: 0 auto;
  text-align: center;

}

.application__header {
  margin: 0;
  letter-spacing: 1px;
  padding-top: 25px;
  font-size: 26px;
  font-weight: bold;
  color: white;
}

.application__description {
  margin-top: 10px;
  font-size: 15px;
  font-weight: lighter;
  letter-spacing: 0.4px;
  color: white;
}

.application__item {
  width: 230px;
  display: flex;
  justify-content: space-between;
  margin-left: 48px;
}
.application__item a {
  font-weight: bold;
  font-size: 20px;
  color: white;
  margin: 0;
  letter-spacing: 1px;
  text-decoration: none;
}

.form {
  width: 320px;
}

.form__wrapper {
  margin-top: 21px;
}

.form__item {
  position: relative;
  padding-bottom: 7px;
  list-style: none;
}

.form__item--1::before {
  content: " ";
  position: absolute;
  top: 16px;
  left: 36px;
  width: 16px;
  height: 16px;
  background-image: url("../img/name.svg");
}

.form__item--2::before {
  content: " ";
  position: absolute;
  top: 15px;
  left: 34px;
  width: 19px;
  height: 18.97px;
  background-image: url("../img/phone.svg");
}

.form__item--3::before {
  content: " ";
  position: absolute;
  top: 18px;
  left: 33px;
  width: 19px;
  height: 14px;
  background-image: url("../img/email.svg");
}


.form__list {
  padding: 0;
  margin: 0;
}

.form__window {
  padding-top: 16px;
  padding-bottom: 17px;
  padding-left: 58px;
  width: 240px;
  border-radius: 6px;
  border: none;
  font-size: 15px;
  font-weight: lighter;

  &--message {
    padding-left: 19px;
    padding-bottom: 34px;
    width: 279px;
  }
}

.form__button-container {
  padding-bottom: 31px;
}

.form__button {
  border: none;
  background-color: #f83659;
  color: white;
  padding: 21px 89px 20px 90px;
  border-radius: 50px;
  cursor: pointer;
  font-weight: lighter;
  font-size: 16px;
  letter-spacing: 1.28px;
}

.form__information {
  width: 295px;
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: 25px;
  padding-top: 0;
  text-align: right;
  margin-left: -15px;
}

.form-list__value {
  font-size: 15px;
  text-align: left;
  color: white;
  font-weight: lighter;
}

.form-list__value::before {
  content: "";
  position: absolute;
  top: 47px;
  left: 67px;
  width: 220px;
  height: 1px;
  background-color: #f83659;
}

.form-logo {
  display: none;
}

@keyframes click-wave {
  0% {
    height: 25px;
    width: 25px;
    opacity: 0.35;
    position: relative;
  }
  100% {
    height: 200px;
    width: 200px;
    margin-left: -80px;
    margin-top: -80px;
    opacity: 0;
  }
}

.option-input {
  appearance: none;
  position: relative;
  top: 13.33333px;
  right: 0;
  bottom: 0;
  left: 0;
  height: 25px;
  width: 25px;
  transition: all 0.15s ease-out 0s;
  background: #cbd1d8;
  border: none;
  color: #f83659;
  cursor: pointer;
  display: inline-block;
  margin-right: 0.5rem;
  outline: none;
  z-index: 1000;
  border-radius: 4px;
}
.option-input:hover {
  background: #9faab7;
}
.option-input:checked {
  background: white;
}
.option-input:checked::before {
  height: 25px;
  width: 25px;
  margin-top: -6px;
  position: absolute;
  content: '✔';
  color: #f83659;
  display: inline-block;
  font-size: 20px;
  text-align: center;
  line-height: 40px;
}
.option-input:checked::after {
  -webkit-animation: click-wave 0.65s;
  -moz-animation: click-wave 0.65s;
  animation: click-wave 0.65s;
  background: #f83659;
  content: '';
  display: block;
  position: relative;
  z-index: 100;
}
.option-input.radio {
  border-radius: 50%;
}
.option-input.radio::after {
  border-radius: 50%;
}

.form-list__value--popup {
  color: black;
}

@media (min-width: $desktop-width) {
  .application__wrapper {
    width: 755px;

  }

  .form__application {
    background-image: none;
    position: relative;
    z-index: 10000;
  }

  .form__block-wrapper {
    background-image: url("../img/bg-form.png");
    background-color: #1f104f;
    background-repeat: no-repeat;
    background-size: cover;
    width: 70%;
  }

  .form__description {
    position: relative;
    display: flex;
    margin-left: 107px;
  }

  .application__description {
    text-align: left;
    margin-top: 33px;
    font-size: 20px;
  }

  .application__header {
    padding-top: 79px;
    font-size: 31px;
  }

  .application__item {
    margin-left: 20px;
    margin-top: 32px;
  }

  .form__list {
    display: grid;
  }



  .form__item {
    padding-bottom: 12px;
  }

  .form__item--4 {
    grid-column-start: 4;
    grid-row-start: 1;
    margin-left: 9px;
  }

  .form__item--2 {
    grid-column-start: 1;
    margin-top: -132px;
  }

  .form__item--3 {
    grid-column-start: 1;
    margin-top: -67px;
  }

  .form__window {
    width: 310px;

    &--message {
      padding-bottom: 130px;
      width: 360px;
    }
  }

  .form__block {
    display: flex;
    justify-content: space-between;
    width: 755px;
    margin-top: 25px;
  }

  .form-logo {
    display: block;
    position: absolute;
    top: 16px;
    left: 731px;
  }
}
