.news__wrapper {
  width: 320px;
  margin: 0 auto;
  margin-bottom: 53px;
}

.news__header {
  margin: 0;
  text-align: center;
  font-size: 26px;
  font-weight: bold;
}

.news__container {
  position: relative;
  margin-top: 39px;
  margin-bottom: 22px;
  text-align: center;
}

.news__data {
  position: absolute;
  top: 181px;
  left: 26px;
  font-size: 13px;
  font-weight: lighter;
  color: white;
}

.news__show {
  position: absolute;
  top: 181px;
  left: 122px;
  width: 45px;
  display: flex;
  justify-content: space-between;
}

.news__show svg {
  margin-top: 2px;
}

.news__number {
  font-size: 13px;
  font-weight: lighter;
  color: white;
}

.news__block-container {
  margin-left: 26px;
  width: 264px;
}

.news__block-header {
  font-size: 15px;
  font-weight: bold;
}

.news__block-description {
  margin-top: 20px;
  margin-bottom: 25px;
  font-size: 14px;
  font-weight: lighter;
}

.news__button {
  margin-left: 25px;
  font-size: 16px;
  letter-spacing: 1.28px;
  font-weight: lighter;
  padding: 17px 65px 18px 65px;
  border: 2px solid #f83659;
  border-radius: 50px;
}

.news__block-number {
  margin-top: 52px;
  margin-left: 112px;
}

.news__block {
  display: none;
}

@media (min-width: $desktop-width) {
  .news {
    margin-bottom: 141px;
  }

  .slider--news {
    display: none;
  }

  .news__block {
    display: flex;
    justify-content: space-between;
  }

  .news__block-number {
    display: none;
  }

  .news__container {
    margin-top: 44px;
    text-align: left;
  }

  .news__wrapper {
    width: 1170px;
  }

  .news__header {
    font-size: 31px;
  }

  .news__block-container {
    margin-left: 20px;
    width: 321px;
  }
}
