.map__wrapper {
  width: 100%;
  margin: 0 auto;
}

.desk {
  display: none;
}

@media (min-width: $desktop-width) {
  .mob {
    display: none;
  }

  .desk {
    display: block;
  }

  .map {
    position: relative;
  }

  .map__wrapper {
    position: absolute;
    right: 1px;
    width: 780px;
    margin-left: auto;
    margin-right: 0;
    margin-top: -240px;
  }
}

#map {width: 100%; height: 400px;}
